"use client"

import type React from "react"

import { useState, useEffect } from "react"
import { centresAPI, patientsAPI, queriesAPI, ordersAPI } from "@/lib/api"

// Types
interface User {
  id: string
  name: string
  email: string
  role: "super_admin" | "centre_admin"
  centreId: string | null
  status?: "pending" | "approved" | "rejected"
  phone?: string
  centreName?: string
  centreAddress?: string
  centreState?: string
  centreCity?: string
  rejectionReason?: string
}

interface Centre {
  id: string
  name: string
  state: string
  city: string
  address: string
  phone: string
  email: string
  capacity: number
  administrator: string
  adminEmail: string
  status: "active" | "inactive"
  createdAt: string
}

interface Patient {
  id: string
  centreId: string
  name: string
  dob: string
  age: number
  gender: string
  phone: string
  email: string
  address: string
  aadharNumber: string
  familyContactName: string
  familyContactPhone: string
  addictionType: string
  admissionDate: string
  status: "admitted" | "under_treatment" | "recovering" | "discharged"
  medications: Medication[]
}

interface Medication {
  id: string
  name: string
  dosage: string
  frequency: string
  prescribedBy: string
  startDate: string
}

interface Query {
  id: string
  centreId: string
  centreName: string
  subject: string
  description: string
  priority: "low" | "medium" | "high" | "urgent"
  status: "open" | "in_progress" | "resolved" | "closed"
  createdBy: string
  createdAt: string
  responses: QueryResponse[]
}

interface QueryResponse {
  id: string
  message: string
  respondedBy: string
  respondedAt: string
  isAdmin: boolean
}

interface Order {
  id: string
  subject: string
  instruction: string
  targetCentreId: string | null
  targetCentreName: string
  priority: "low" | "medium" | "high" | "critical"
  status: "issued" | "acknowledged" | "in_progress" | "completed"
  deadline: string
  issuedBy: string
  issuedAt: string
  acknowledgements: Acknowledgement[]
}

interface Acknowledgement {
  centreId: string
  acknowledgedBy: string
  acknowledgedAt: string
}

